    body,
    .jumbotron {
        background: $t-background !important;
        color: $t-color;
    }

    .bt-nav {
        background-color: $t-background;
        // border-top: 1px solid $t-border-color-nav;

        // 0px -4px 10px rgba(0, 0, 0, 0.1);
        // 0px -4px 10px rgba(0, 0, 0, 0.1)
        -webkit-box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.1);
        box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.1);

        .bt-tabs {
            ul.bt-nav__ul {
                li.bt-nav__item {
                    .bt-nav__icon {
                        color: rgba(0, 0, 0, 0.8);
                        &.active {
                            color: #117dfc;
                        }
                    }
                }
            }
        }

        a {
            color: $t-color;
            text-decoration: none;
        }

        a:focus {
            color: $t-color;
            text-decoration: none;
        }
    }

    nav.bg-dark {
        background: $t-background !important;
    }

    .logout {
        color: rgb(255, 43, 43);
    }

    .nav-side-container {
        .side-nav {
            &.opened {
                .closer {
                    background-color: #0000009d;
                }
            }

            .n-u-container {
                .n-avatar-image {
                    background: #fff !important;
                }

                .n-name {
                }

                .n-username {
                }
                .n-items {
                    color: $t-color;
                    a,
                    a:hover,
                    a:focus {
                        // color: $t-color;
                        &.logout {
                            color: rgb(255, 43, 43);
                        }

                        &.l-item {
                            color: $t-color;
                        }
                    }
                }
                .n-b-items {
                }
            }
            .contents {
                background-color: $t-background;
                color: $t-color;
            }

            .closer {
            }

            .icon-bar {
                background-color: darken($t-color, 10%);
            }
        }
    }

    .nav-custom-container {
        // border-bottom: 1px solid $t-border-color-nav;
        // rgba(0,0,0,0.1)
        -webkit-box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);

        background-color: $t-background;
        .nav-btn.active {
            background-color: $dropdown-active;
        }
        .nav-btn:hover {
            background-color: $dropdown-hover;
        }
    }

    .navbar {
        background-color: $t-background !important;
