    }

    .form-control:focus,
    .form-control:hover,
    .form-control,
    input.form-control,
    input.form-control:hover,
    input.form-control:focus,
    textarea.form-control,
    textarea.form-control:hover,
    textarea.form-control:focus {
        background-color: $t-foreground;
        color: $input-color;
    }

