    .custom-card {
        background-color: $t-foreground !important;

        .u-name {
            color: $t-color;
        }

        .u-username {
            color: #999;
        }

        .text-banner {
            color: $t-foreground;
        }

        .card-content {
            .title {
                color: $t-color;
            }

            .time {
                color: #bdbdbd80;
            }

            .content {
                color: #aaaaaa;
            }

            .tags {
                color: #a5a5a580;
            }
        }
    }

    svg.action-icon {
        path,
        rect {
            fill: lighten(#000, $amount: 30%) !important;
        }
    }

    // svg.text{
    //     path,
    //     rect{
    //         fill: $t-color;
    //     }
    // }
    
    .custom-card2 {
        -webkit-box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);

        background-color: $t-foreground;

        .drop-btn {
            color: $t-color;
            background-color: $t-foreground;
        }
        .drop-btn.active,
        .drop-btn:focus {
            color: $t-color;
            // background-color: $dropdown-active;
        }
        .drop-btn:hover {
            color: $t-color;
            // background-color: $dropdown-hover;
        }

        .u-name {
            color: $t-color;
        }

        .u-username {
            color: #999;
        }

       

        svg.action-icon.liked {
            path,
            rect {
                fill: rgb(240, 70, 70) !important;
            }
        }

        svg.action-icon.bookmarked {
            path,
            rect {
                fill: #2196f3 !important;
            }
        }
    }

    hr.light {
        background: #ddd !important;
    }
