                    &.nav-btn:hover {
                        background-color: $dropdown-hover;
                    }
                }

                .nav-link.active {
                    background-color: darken($t-foreground, 5%);
                    color: $t-color;
                    padding-left: 24px;
                    border-left: 6px solid $t-nav-pill-color;
                }
            }
        }
